header {
  z-index: 200 !important;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height:60px;
  /* height: 5em; */
  background-color: #ffffff;
  border-bottom: #ccc 2px solid;
  color: #414141;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10%;
  /* box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25); */
  z-index: 10;
  font-size: .75em;
}

.header h2{
  font-size: 1em;
}


.link{
  font-size: 1em;
  color: #414141;
  text-decoration: none !important;
  font-weight: 600;
  
}

.link:active {
  color: #414141;
  text-decoration: none !important;
}

.cart-link{
  position: relative;
}

.badge{
  
  width: 24px;
  height: 24px;
  border-radius: 50%;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;

  position: absolute;
  top: -.5rem;
  right: -1.5rem;
}

@media (min-width: 769px) { 
  .header {
    height: 80px;
    font-size: 1em;
  }
}


